.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: rgba(63, 102, 143, 0.8); /* Light blue */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
}

.form-group {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  font-size: 1.1rem;
}

input, textarea {
  width: 100%;
  padding: 10px;
  border: 2px solid #007BFF; /* Light blue */
  border-radius: 5px;
  font-size: 1rem;
  margin-top: 5px;
  background-color: white;
  color: #333;
}

textarea {
  resize: vertical;
}

button {
  padding: 10px 20px;
  font-size: 1rem;
  background-color: #4CAF50; /* Green */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
}

.notification {
  margin-top: 20px;
  padding: 10px;
  border-radius: 5px;
  color: white;
  text-align: center;
  width: 100%;
}

.notification.success {
  background-color: green;
}

.notification.error {
  background-color: red;
}
